.section-container {
  margin: 60px 20px 0;
  text-align: left;
}

.section-content {
  text-align: center;
}

.detail_container {
  width: 80%;
  height: 100vh;
  display: flex;
  margin: 30px auto;
}

.detail_poster_container {
  width: 40%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.detail_poster_inner_container {
  width: 100%;
  height: 100%;
}

.detail_poster {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.detail_main_container {
  display: flex;
  align-items: center;
  height: 100%;
  width: 60%;
  /* border: 1px solid blue; */
  padding: 0 10px;
  text-align: left;
}

.detail_main_inner_container {
  height: 70%;
  width: 100%;
}

.detail_info {
  width: 100%;
  height: 100%;
}

.detail_info h2 {
  margin: 0;
}

.detail_sub_info {
  margin-top: 10px;
}

.detail_button {
  border: none;
  font-weight: 600;
  margin-left: 3px;
  border-radius: 5px;
  background-color: rgb(232, 183, 8);
  outline: none;
  cursor: pointer;
}

.detail_button a {
  padding: 3px 5px;
}

.detail_grid_container {
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow-x: hidden;
}

.detail_tab_row {
  display: flex;
  flex-direction: row;
}

.detail_tab_col {
  /* border: 1px solid white; */
  background-color: #888;
  opacity: 0.5;
  padding: 5px;
  font-size: 20px;
  cursor: pointer;
}

.detail_grid_content {
  padding: 20px;
  display: none;
  width: 100%;
  overflow-x: hidden;
}

.detail_grid_content.show {
  display: block;
  background-color: #999;
  width: 100%;
  overflow-x: hidden;
}

.detail_tab_col.show {
  background-color: #999;
  opacity: 1;
}

iframe {
  border: none;
}

.detail_company_container {
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}

.detail_companies {
  margin-top: 10px;
  display: flex;
  flex-direction: row;
  /* justify-content: space-around; */
  width: 80%;
  overflow-x: auto;
  /* border: 1px solid red; */
}

.detail_company {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.detail_company_name {
  text-align: center;
  margin-top: 0;
  height: 20px;
}

.detail_logo_container {
  width: 150px;
  height: 100px;
  display: flex;
  justify-content: center;
}

.detail_logo {
  width: 80%;
  height: 80%;
  margin: 0 auto;
}

.detail_country_container {
  margin-top: 15px;
}

.detail_country_img_container {
  display: flex;
  flex-wrap: wrap;
}

.nav-list-container {
  width: 100%;
  height: 50px;
  padding: 0 10px;
  background-color: rgba(20, 20, 20, 0.8);
  box-shadow: rgba(0, 0, 0, 0.8) 0px 1px 5px 2px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  /* border: 1px solid red; */
}

.nav-ul {
  display: flex;
  align-items: center;
  list-style-type: none;
  border: 1px solid white;
  padding: 0 10px;
  width: 30%;
  border-width: 0;
  margin: 0;
}

.nav-ul li {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30%;
  height: 100%;
  cursor: pointer;
}

.nav-list-container li:first-child {
  margin-left: 0;
}

a {
  text-decoration: none;
  color: #fff;
  padding: 14px 16px;
  font-size: 17px;
  border-bottom: 3px solid transparent;
}

a.nav-activated {
  border-bottom: 3px solid red;
}

